@import "../base/mixins.scss";
@import "../base/vars.scss";

.contact {
  padding: 100px 0 110px;

  background: $color_blue_sky;
}

.contact__inner {
  @include fixed_inner;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  &--wide {
    padding: $padding_desktop_wide;
  }
}

.contact__image {
  flex-basis: 45%;
  min-height: 300px;
  margin: 0 auto 0 0;

  background: transparent url("../img/plane-desktop@1x.png") no-repeat center;
  background-size: contain;
}
